import * as React from "react";
import Taglist from './Taglist';
import Stash from './Stash';
import '../styles/Nav';
import * as Backend from '../Backend';

export interface NavProps { }
export interface NavState { }

export class Nav extends React.Component<NavProps, NavState> {
    render() {
        let rclick = (event : any) => { this.handleRandomClick(event); };
        let rtclick = (event : any) => { this.handleRandomTaggedClick(event); };
        let l7click = (event : any) => { this.handleLast7Clicked(event); };
        let tophone = (event : any) => { this.handleToPhoneClick(event); };
        let chromecast = (event : any) => {this.handleChromecastClick(event); };
        let desk = (event : any) => { this.handleDeskClick(event); };
        return <div className="component-nav">
            <button onClick={l7click} className="component-nav-last7">last 7</button>
            <button onClick={rclick} className="component-nav-random">random</button>
            <button onClick={rtclick} className="component-nav-random-tagged">random tagged</button>
            <Taglist/>
            <button onClick={tophone} className="component-nav-tophone">tophone</button>
            <button onClick={chromecast} className="component-nav-chromecast">chromecast</button>
            <button onClick={desk} className="component-nav-chromecast">desk</button>
            </div>;
    }
    handleLast7Clicked(event : any) {
        let d = new Date();
        d.setDate(d.getDate() - 7);
        let zpad = (s : number) => { let ret = s + ""; while(ret.length < 2) ret = '0' + ret; return ret; };
        let q = "added:" + d.getFullYear() + "-" + zpad(d.getMonth() + 1) + "-" + zpad(d.getDate()) + "..";
        console.log(q);
        Backend.Server.SearchForSongs(q);
    }
    handleRandomClick(event : any) {
        Backend.Server.GetRandomSongs(50);
    }
    handleRandomTaggedClick(event : any) {
        Backend.Server.GetRandomTaggedSongs(50);
    }
    handleToPhoneClick(event : any) {
        Backend.Server.LoadAugysPhone();
    }
    handleChromecastClick(event : any) {
        Backend.Server.SwitchToChromecast();
    }
    handleDeskClick(event : any) {
        Backend.Server.SwitchToDesk();
    }
}

export default Nav;



