import Tag from './Tag'

export default class Song {
    artist: string;
    title: string;
    album: string;
    year: string;
    length: string;
    track: string;
    id: string;
    format: string;
    bitrate: string;
    path: string;
    tags: Tag[];
    constructor() {
        this.artist = "";
        this.title = "";
        this.album = "";
        this.year = "";
        this.length = "";
        this.track = "";
        this.id = "";
        this.format = "";
        this.bitrate = "";
        this.path = "";
        this.tags = [];
    }
};



