import * as React from 'react';
import PopPlayer from '../pop/Player';
import Player from '../state/Player';
import '../styles/Transport';
import * as Backend from '../Backend';
import Signals from '../state/Signals';

export interface TransportProps { }
export interface TransportState { player : PopPlayer }

export class Transport extends React.Component<TransportProps, TransportState> {
    private random: number;
    private playerTimer: any;
    private scrubber: HTMLElement;
    private searchInput: HTMLElement;
    constructor(props : TransportProps) {
        super(props);
        this.state = { player: null }
        this.random = Math.random();
    }

    private getProgressPercent() : number {
        if (this.state.player && this.state.player.position) {
            let prog = (this.state.player.position / this.state.player.duration) * 100;
            return prog;
        }
        return 1;
    }

    render() {
        let progress : any = { width: this.getProgressPercent() + "%" };
        let scrub = (event : any) => { this.handleClicked(event); };
        let scrubref = (obj : HTMLElement) => { this.scrubber = obj; };

        let play = (event : any) => { this.handlePlayClicked(event); };
        let shuffle = (event : any) => { this.handleShuffleClicked(event); };
        let track = (event : any) => { this.handleTrackClicked(event); };

        let searchref = (obj : HTMLElement) => { this.searchInput = obj; };
        let keyup = (event : any) => this.searchKeyUp(event);

        return <div className="component-transport">
            <button className="component-transport-play" onClick={play}>{this.playStatus()}</button>
            <button className="component-transport-shuffle" onClick={shuffle}>{this.shuffleStatus()}</button>
            <button className="component-transport-track" onClick={track}>{this.trackString()}</button>
            <input className="component-transport-search" ref={searchref} onKeyUp={keyup} placeholder="Search . . ."></input>
            <div className="component-transport-numbers">{this.positionString()}</div>
            <div onClick={scrub} ref={scrubref} className="component-transport-progress-background">
                <div style={progress} className="component-transport-progress-bar"></div>
            </div>
        </div>;
    }

    playStatus() : string {
        if (this.state.player) {
            return this.state.player.status;
        }
        return "dunno";
    }

    shuffleStatus() : string {
        if (this.state.player) {
            return this.state.player.shuffle? "shuffling" : "not shuffling";
        }
        return "dunno";
    }

    positionString() : string {
        if (this.state.player) {
            return this.state.player.position + " / " + this.state.player.duration;
        }
        return "dunno";
    }

    trackString() : string {
        if (this.state.player) {
            let p = this.state.player;
            if (p) {
                return p.artist + " - " + p.title + " (" + p.album + ")";
            }
        }
        return "dunno";
    }

    handlePlayClicked(event : any) {
        if (Player.isPlaying()) {
            Backend.Server.Pause();
        } else {
            Backend.Server.Play();
        }
    }

    handleShuffleClicked(event : any) {
        Backend.Server.Shuffle();
    }

    handleTrackClicked(event : any) {
        Signals.trackShowByPath(this.state.player.path);
    }

    handleClicked(event : any) {
        var x = event.nativeEvent.offsetX;
        var pos = event.nativeEvent.offsetX / this.scrubber.offsetWidth;
        pos = Math.round(pos * this.state.player.duration);
        Backend.Server.SeekToSecond(pos);
    }

    private getCallbackId() {
        return "Transport_" + this.random;
    }
    componentDidMount() {
        this.setState({ player: Player.state });
        Player.update.addCallback
        (
            {
                id: this.getCallbackId(),
                cb: () => this.setState({ player: Player.state })
            }
        );

        let whilePlaying = () => {
            if (this.playerTimer) {
                clearTimeout(this.playerTimer);
                this.playerTimer = null;
            }
            let cpy = JSON.parse(JSON.stringify(Player.state)) as PopPlayer;
            if (cpy.position % 10 === 0 ||
                cpy.position >= cpy.duration) {
                Backend.Server.UpdatePlayerStatus();
            } else {
                cpy.position += 1;
                Player.setState(cpy);
            }
            this.playerTimer = setTimeout(whilePlaying, 1000);
        };
        // when we start playing, set up a timer to track playback position
        Player.startPlaying.addCallback
        (
            {
                id: this.getCallbackId(),
                cb: () => {
                    whilePlaying();
                    this.setState({ player: Player.state });
                }
            }
        );
        // in case the player is already playing...
        if (Player.isPlaying()) whilePlaying();

        // when we stop playing, clear the timer
        Player.pause.addCallback
        (
            {
                id: this.getCallbackId(),
                cb: () => {
                    if (this.playerTimer) {
                        clearTimeout(this.playerTimer);
                        this.playerTimer = null;
                    }
                }
            }
        );
        Backend.Server.UpdatePlayerStatus();
    }
    componentWillUnmount() {
        Player.update.removeCallback(this.getCallbackId());
    }

    searchKeyUp(event : any) {
        if (event.keyCode === 13) {
            let input = this.searchInput as any;
            Backend.Server.SearchForSongs(input.value);
        }
    }
}

export default Transport;



