import * as React from "react";
import * as Backend from '../Backend';
import PopSong from '../pop/Song'
import PopTag from '../pop/Tag';
import '../styles/SongDetail';
import Signals from '../state/Signals';

export interface SongDetailProps { }
export interface SongDetailState { song : PopSong; }

export class SongDetail extends React.Component<SongDetailProps, SongDetailState> {
    private random: number;
    constructor(props : SongDetailProps) {
        super(props);
        this.random = Math.random();
        this.state = { song: null };
    }
    componentDidMount() {
        Signals.trackInfoEvent.addCallback
        (
            {
                id: 'SongDetail_' + this.random,
                cb: () => { this.setSong(Signals.trackInfoSong); }
            }
        );
    }
    componentWillUnmount() {
        Signals.trackInfoEvent.removeCallback('SongDetail_' + this.random);
    }
    render() {
        let s = this.state.song;
        if (!s) {
            s = new PopSong();
        }
        let qartist = (e : any) => this.searchArtist();
        let qtitle = (e : any) => this.searchTitle();
        let qalbum = (e : any) => this.searchAlbum();
        let qyear = (e : any) => this.searchYear();
        let qlength = (e : any) => this.searchLength();
        let qtrack = (e : any) => this.searchTrack();
        let qid = (e : any) => this.searchId();
        let qformat = (e : any) => this.searchFormat();
        let qbitrate = (e : any) => this.searchBitrate();
        let qpath = (e : any) => this.searchPath();
        let qtags = (e : any) => this.searchTags();

        return <div className="component-song-detail">
            <button onClick={qartist}>Artist: {s.artist}</button>
            <button onClick={qtitle}>Title: {s.title}</button>
            <button onClick={qalbum}>Album: {s.album}</button>
            <button onClick={qyear}>Year: {s.year}</button>
            <button onClick={qlength}>Length: {s.length}</button>
            <button onClick={qtrack}>Track: {s.track}</button>
            <button onClick={qid}>ID: {s.id}</button>
            <button onClick={qformat}>Format: {s.format}</button>
            <button onClick={qbitrate}>Bitrate: {s.bitrate}</button>
            <button onClick={qpath}>Path: {s.path}</button>
            <button onClick={qtags}>Tags: {this.tagString(s)}</button>
            </div>;
    }
    private tagString(song : PopSong) : string {
        let s : string[] = [];
        song.tags.forEach((t : PopTag) => s.push(t.name));
        return s.join(", ");
    }
    setSong(s : PopSong) {
        this.setState({ song: s });
    }
    private q(field : string) {
        let fields = this.state.song as any;
        Backend.Server.SearchForSongs(field + ":" + fields[field]);
    }
    searchArtist() {
        this.q("artist");
    }
    searchTitle() {
        this.q("title");
    }
    searchAlbum() {
        this.q("album");
    }
    searchYear() {
        this.q("year");
    }
    searchLength() {
        this.q("length");
    }
    searchTrack() {
        this.q("track");
    }
    searchId() {
        this.q("id");
    }
    searchFormat() {
        this.q("format");
    }
    searchBitrate() {
        this.q("bitrate");
    }
    searchPath() {
        this.q("path");
    }
    searchTags() {
        console.log("No tag search--use left side tag nav");
    }
}

export default SongDetail;



