// provides a simple key/value store for browser-saved settings
import * as Events from './Event'

class _LocalSettings {
    kv : { [key: string]: string };
    events : { [key : string]: Events.Event };
    constructor() {
        if (typeof localStorage !== 'undefined') {
            this.kv = JSON.parse(localStorage.getItem("Settings"));
        } else {
            console.log("localStorage not defined");
            this.kv = {};
        }
        if (!this.kv) {
            this.kv = {};
        }
        this.events = {};
    }

    get(k : string, def : any) {
        let ret = this.kv[k];
        if (typeof ret === 'undefined') {
            return def;
        }
        return ret;
    }

    set(k : string, v : any) {
        this.kv[k] = v;
        let e = this.events[k];
        if (typeof e !== 'undefined') {
            e.trigger();
        }
        if (typeof localStorage !== undefined) {
            localStorage.setItem("Settings", JSON.stringify(this.kv));
        }
    }

    notify(k : string, cb : Events.EventCallback) {
        let e = this.events[k];
        if (typeof e === 'undefined') {
            e = new Events.Event(k + "Changed");
            this.events[k] = e;
        }
        e.addCallback(cb);
    }
}

const LocalSettings = new _LocalSettings();
export default LocalSettings;



