import * as React from "react";
import * as Const from '../const';
import Nav from './Nav';
import Songlist from './Songlist';
import Transport from './Transport';
import SongDetail from './SongDetail';
import Signals from '../state/Signals';
import '../styles/Pop';

export interface PopProps { }
export interface PopState { }

export class Pop extends React.Component<PopProps, PopState> {
    render() {
        return <div className="component-pop">
                <Nav/>
                <Transport/>
                <SongDetail/>
                <Songlist/>
                <input className="component-pop-input-sink" autoFocus={true}/>
            </div>;
    }
    componentDidMount()
    {
        document.addEventListener('keydown', this.handleKeyDown);
    }
    componentWillUnmount()
    {
        document.removeEventListener('keydown', this.handleKeyDown);
    }
    handleKeyDown(e : any)
    {
        if (e.target && e.target.tagName)
        {
            if (e.target.tagName.toLowerCase() === 'input') return;
            if (e.target.tagName.toLowerCase() === 'textarea') return;
        }
        let key = (e && e.key && e.key.toLowerCase()) || null;
        if (!key) return;
        if (key === 'x')
        {
            Signals.selectCurrentSong();
        }
        else if (key === 'a')
        {
            Signals.selectAllSongs();
        }
        else if (key === 'l')
        {
            Signals.deselectAllSongs();
        }
        else if (key === 's')
        {
            if (e.shiftKey)
                Signals.unstashSelectedSongs();
            else
                Signals.stashSelectedSongs();
        }
    }
}



