import * as React from 'react';
import PopSong from '../pop/Song';
import Song from './Song';
import Queue from '../state/Queue';
import * as Backend from '../Backend';
import Signals from '../state/Signals';
import '../styles/Songlist';

export interface SonglistProps { }
export interface SonglistState { songs: PopSong[]; field: string; }

export class Songlist extends React.Component<SonglistProps, SonglistState> {
    constructor(props : SonglistProps) {
        super(props);
        this.selectableFields =
            [
                'tags',
                'artist',
                'title',
                'album',
                'year',
                'length',
                'track',
                'id',
                'format',
                'bitrate'
            ];
        this.state = { songs: [], field: 'tags' };
        this.random = Math.random();
    }
    random: number;
    componentDidMount() {
        Queue.update.addCallback
        (
            {
                id: this.random + '_SonglistCallback',
                cb: () => this.setSongs(Queue.songs)
            }
        );
        Signals.fieldSelectedEvent.addCallback(
            {
                id: 'Songlist_' + this.random,
                cb: () => this.setState(
                    {
                        songs: this.state.songs,
                        field: Signals.fieldSelectedField
                    })
            });
        Backend.Server.GetCmusPlaylist();
    }
    componentWillUnmount() {
        Queue.update.removeCallback(this.random + "_SonglistCallback");
    }
    private selectableFields : string[];
    render() {
        let sorted = this.state.songs.slice(); // shallow copy songs
        sorted.sort((s1 : any, s2 : any) => { // actually PopSong, but need ability to access via state.field
            if (this.state.field === 'tags') {
                let i1 = this.state.songs.indexOf(s1);
                let i2 = this.state.songs.indexOf(s2);
                return i1 < i2? -1 : 1;
            }
            if (!s1[this.state.field])
            {
                console.log(this.state.field + ' not in ' + JSON.stringify(s1));
            }
            return s1[this.state.field].localeCompare(s2[this.state.field]);
        });

        // field selector
        let fields = () => {
            return this.selectableFields.map(f => <option key={f} value={f}>{f}</option>);
        };
        let change = (event: any) => this.handleOnChange(event);
        let selector = 
            <select onChange={change} className="component-songlist-select">{fields()}</select>;

        // TODO: If the songlist is sorted, it will not match the playlist of Cmus, and so
        // will play in the default order. This will be confusing.
        let i = 0;
        let f = sorted.map(
            s =>
                <Song
                    key={s.path}
                    song={s}
                    playlistIndex={i++}
                    field={this.state.field}
                />
        );

        return <div className="component-songlist">
            {selector}
            {f}
            </div>;
    }
    handleOnChange(event : any) {
        Signals.fieldSelected(event.target.value);
    }
    fieldSelected() {
        this.setState({ songs: this.state.songs, field: Signals.fieldSelectedField });
    }
    setSongs(songs : PopSong[]) {
        this.setState({ songs: songs, field: this.state.field });
    }
    componentDidUpdate() {
        document.body.scrollTop = 0; // For Chrome, Safari and Opera 
        document.documentElement.scrollTop = 0; // For IE and Firefox
    }
}

export default Songlist;



