import Song from './pop/Song'
import Tag from './pop/Tag'
import Tags from './state/Tags'
import Query from './state/Query'
import Queue from './state/Queue'
import Player from './state/Player'
import PopPlayer from './pop/Player'
import Stash from './state/Stash'
import Settings from './state/LocalSettings'
// TODO: Playlists
import * as Cmus from './cmus/cmus'

type ResponseCallback = (response: any) => void;
interface Request { path: string; callback: ResponseCallback; }

function makeArgs(args : string[]) {
   let ret = [];
   for (var i = 0; i < args.length; i += 2) {
      ret.push(encodeURIComponent(args[i])
               + "=" +
               encodeURIComponent(args[i+1]));
   }
   return ret.join("&");
}

Query.update.addCallback({
    id: "AutoLoadQueue",
    cb: function() {
        Queue.setSongs(Query.songs);
    }
});

export class BackendImpl {
    constructor() {
        this.requestCount = 0;
        this.totalRequests = 0;
    }

    private requestCount: number;
    totalRequests: number;

    private requestStart() {
        ++this.requestCount;
        ++this.totalRequests;
    }

    private requestFinish() {
        --this.requestCount;
    }

    playerStatusReceived(response : any) {
        let s = JSON.parse(response) as Cmus.PlayerResponse;
        let state = new PopPlayer();
        state.status = s.status;
        state.title = s.title;
        state.artist = s.artist;
        state.album = s.album;
        state.path = s.path;
        state.duration = parseInt(s.duration);
        state.position = parseInt(s.position);
        state.shuffle = s.shuffle === "true";
        Player.setState(state);
    }


    private queueStatusReceived(response : any) {
        let qs = JSON.parse(response) as Cmus.QueryResponse;
        Queue.setSongs(this.cmusToPopSongs(qs.songs));
    }

    private cmusToPopSongs(cmus : Cmus.SongResponse[]) : Song[] {
        let ret : Song[] = [];
        cmus.forEach(s => { ret.push(this.cmusToPopSong(s)); });
        return ret;
    }

    private cmusToPopSong(cmus : Cmus.SongResponse) : Song {
        let tags : Tag[] = [];
        // convert tag names to actual Tags
        cmus.tags.forEach( (tname : string) => {
            let t = Tags.getTag(tname);
            if (t) {
                tags.push(t);
            }
        });
        let song = new Song;
        // copy fields into a song
        song.artist = cmus.artist;
        song.title = cmus.title;
        song.album = cmus.album;
        song.year = cmus.year;
        song.length = cmus.length;
        song.track = cmus.track;
        song.id = cmus.id;
        song.format = cmus.format;
        song.bitrate = cmus.bitrate;
        song.path = cmus.path;
        song.tags = tags;
        return song;
    }

    private cmusToPopTag(t : Cmus.TagResponse) {
        return new Tag(t.name, t.count);
    }

    private cmusToPopTags(tags : Cmus.TagResponse[]) {
        let popTags : Tag[] = [];
        for (let i = 0; i < tags.length; ++i) {
            let t = Tags.getTag(tags[i].name);
            if (t === null) {
                t = this.cmusToPopTag(tags[i]);
            } else {
                t.count = tags[i].count;
            }
            popTags.push(t);
        }
        return popTags;
    }

    private tagsReceived(response : any) {
        let parsed = JSON.parse(response) as Cmus.TagResponse[];
        parsed.sort((a : any, b : any) => { return a.name.localeCompare(b.name); });
        Tags.setTags(this.cmusToPopTags(parsed));
    }

    requestAndUpdatePlayerStatus(req : string) {
        var This = this;
        var cb = function(response : any) {
            This.playerStatusReceived(response);
        }
        this.request({ path: req, callback: cb });
    }

    requestAndUpdateQueueStatus(req : string) {
        var This = this;
        var cb = function(response : any) {
            This.queueStatusReceived(response);
        }
        this.request({ path: req, callback: cb });
    }

    requestAndUpdateTags(req : string) {
        var This = this;
        var cb = function(response : any) {
            This.tagsReceived(response);
        }
        this.request({ path: req, callback: cb });
    }

    requestAndInitCmusPlaylist(req : string) {
        var This = this;
        var cb = function(response : any) {
            This.queueStatusReceived(response);

            // _after_ receiving the initial cmus playlist, hook things up
            // so that whenever we update the queue, we will also update
            // the cmus playlist.
            Queue.update.removeCallback("KeepCmusInSyncWithQueue");
            Queue.update.addCallback({
                id: "KeepCmusInSyncWithQueue",
                cb: function() {
                    Server.SetCmusPlaylist(Queue.songs);
                }
            });
        };
        this.request({ path: req, callback: cb });
    }

    // gets the cmus remote url
    public serverUrl()
    {
        // NOTE: If you wanted to redirect everything to a different server,
        // you would do that here.
        //return "http://" + window.location.hostname+ ":8000" + "/cmus";
        return "http://pi:8000/cmus";
        //return "cmus";
    }

    private request(req : Request) {
        this.requestStart();
        var r = new XMLHttpRequest();
        var rstr = this.serverUrl() + "/" + req.path;
        var This = this;
        r.onreadystatechange = function()
        {
            if (this.readyState === 4)
            {
                if (this.status === 200) {
                    This.requestFinish();
                    if (req.callback) req.callback (this.responseText);
                } else {
                }
            }
        }

        r.open("GET", rstr, true);
        r.setRequestHeader("Cache-Control", "no-cache");
        r.send();
    }

    private post(req : Request, data : any) {
        this.requestStart();
        var r = new XMLHttpRequest();
        var rstr = this.serverUrl() + "/" + req.path;
        var This = this;
        r.onreadystatechange = function()
        {
            if (this.readyState === 4)
            {
                if (this.status === 200) {
                    This.requestFinish();
                    if (req.callback) req.callback (this.responseText);
                } else {
                }
            }
        }

        r.open("POST", rstr, true);
        r.setRequestHeader("Cache-Control", "no-cache");
        r.send(data);
    }

    Play() {
        this.requestAndUpdatePlayerStatus("play");
    }

    Shuffle() {
        this.requestAndUpdatePlayerStatus("shuffle");
    }

    Pause() {
        this.requestAndUpdatePlayerStatus("pause");
    }

    NextSong() {
        this.requestAndUpdatePlayerStatus("next");
    }

    PreviousSong() {
        this.requestAndUpdatePlayerStatus("prev");
    }

    /*
    // You probably shouldn't be calling this, because
    // if you supply a callback, you will get the
    // raw status response from the backend passed to
    // your callback. You'd probably rather call
    // UpdatePlayerStatus() and response to NewPlayerStatus
    // event.
    GetPlayerStatus(callback) {
        this.request("status", callback);
    }
    */

    UpdatePlayerStatus() {
        this.requestAndUpdatePlayerStatus("status");
    }

    UpdateTags() {
        this.requestAndUpdateTags("tagStatus");
    }

    FavoriteCurrentSong() {
        this.request({ path: "fav", callback: function(){} }); // elicits no response
    }

    SeekForward1Minute() {
        this.requestAndUpdatePlayerStatus("f1m");
    }

    SeekBackward1Minute() {
        this.requestAndUpdatePlayerStatus("b1m");
    }

    SeekToSecond(second : number) {
        let s = "" + second;
        this.requestAndUpdatePlayerStatus("seekto?" + makeArgs(["s", s]));
    }

    _getStash() : Tag {
        let tag = Tags.getTag(Settings.get("defaultStash", "_augystash"));
        if (!tag) {
            throw "No tag named " + Settings.get("defaultStash", "_augystash");
        }
        return tag;
    }

    AddSongToStash(song : Song) {
        this.TagSong(this._getStash(), song);
    }
    AddSongsToStash(songs : Song[]) {
        this.TagSongs(this._getStash(), songs);
    }

    RemoveSongFromStash(song : Song) {
        this.UntagSong(this._getStash(), song);
    }

    QueueJump(song : Song) {
        this.requestAndUpdatePlayerStatus("queueJump?" + makeArgs(['path', song.path]));
    }

    // NOTE: This only works because getPlaylist happens to return { name: string, songs: Cmus.Song[] },
    // which matches the return from enqueue/dequeue/etc. (i.e., the other stash related calls).
    SelectStash(name : string) {
        Settings.set("defaultStash", name);
        let t = Tags.getTag(name);
        if (t)
            this.SearchForTag(t);
    }

    TagSong(tag : Tag, song : Song) {
        song.tags.push(tag);
        this.requestAndUpdateTags("tag?" + makeArgs(['tag', tag.name, 'path', song.path]));
    }

    TagSongs(tag : Tag, songs : Song[]) {
        var args = ['tag', tag.name];
        songs.forEach((s) => { args.push("path"); args.push(s.path); s.tags.push(tag); });
        this.requestAndUpdateTags("tag?" + makeArgs(args));
    }

    // array removal
    private arem <T> (a : T[], v : T) {
        var i = a.indexOf(v);
        if (i >= 0) {
            a.splice(i, 1);
        }
    }

    UntagSong(tag : Tag, song : Song) {
        this.arem(song.tags, tag);
        this.requestAndUpdateTags("untag?" + makeArgs(['tag', tag.name, 'path', song.path]));
    }

    DeleteTag(tag : Tag) {
        this.requestAndUpdateTags("tagDelete?" + makeArgs(['tag', tag.name]));
    }

    UntagSongs(tag : Tag, songs : Song[]) {
        var args = ['tag', tag.name];
        songs.forEach((s) => { args.push("path"); args.push(s.path); this.arem(s.tags, tag); });
        this.requestAndUpdateTags("untag?" + makeArgs(args));
    }

    // args=name,value,name,value,etc
    RunServerSideTool(args : string[], callback : ResponseCallback) {
        this.request({ path: "tool?" + makeArgs(args), callback: callback });
    }

    SwitchToChromecast() {
        this.RunServerSideTool(['name', 'chromecast'], () => {});
    }
    SwitchToDesk() {
        this.RunServerSideTool(['name', 'desk'], () => {});
    }

    //
    // NOTE: These queries-that-accept-callbacks need to change. There should
    // be the ui-code-facing layer, which just takes inputs and hardcodes the
    // callbacks; those callbacks update backend state and trigger events to
    // notify ui code when things change.
    //

    private requestAndReceiveQuery(path : string) {

        let cb = (res : any) => {
            let parsed = JSON.parse(res) as Cmus.QueryResponse;
            Query.setSongs(this.cmusToPopSongs(parsed.songs));
        };

        this.request({ path: path, callback: cb });
    }

    // returns something like { songs: [ song1, song2, ... ] }
    SearchForSongs(query : string) {
        this.requestAndReceiveQuery("search?" + makeArgs(["q", query]));
    }

    SearchForTag(tag : Tag) {
        this.requestAndReceiveQuery("tagFetch?" + makeArgs(["tag", tag.name]));
    }

    GetRandomTaggedSongs(n : number) {
        let nstr : string = "" + n;
        this.requestAndReceiveQuery("randomTagged?" + makeArgs(["n", nstr]));
    }

    // same return as SearchForSongs
    GetRandomSongs(n : number) {
        let nstr : string = "" + n;
        this.requestAndReceiveQuery("random?" + makeArgs(["n", nstr]));
    }

    // This is used to tell cmus what to put in its "now playing" or "current" playlist.
    // We ignore the response
    SetCmusPlaylist(songs : Song[]) {
        let args : string[] = [];
        let cb = () => {};
        songs.forEach(s => args.push(s.path));
        this.post
        (
            {
                path: "setMain?" + makeArgs(['bodyFormat', 'NewlineSeparatedPaths']),
                callback: cb
            }
            ,
            args.join("\n")
        );
    }

    SetCmusPlaylistPos(pos : number) {
        let p : string = "" + pos;
        this.requestAndUpdatePlayerStatus("setMainPos?" + makeArgs(['pos', p]));
    }

    // NOTE: this should only be called once on page startup. probably.
    GetCmusPlaylist() {
        this.requestAndInitCmusPlaylist("getMain");
    }

    LoadAugysPhone() {
        this.request( { path: "loadAugysPhone", callback: (response : any) => {
            console.log(response);
        } });
    }

    LoadStellasPhone() {
        this.request({ path: "loadStellasPhone", callback: (response : any) => {
            console.log(response);
        } });
    }
}

export var Server = new BackendImpl();

export default Server;



