import * as React from "react"
import Tag from './Tag'
import * as Backend from '../Backend'
import Tags from '../state/Tags'
import PopTag from '../pop/Tag'
import '../styles/Taglist'

// a basic object we get from the server
export interface TaglistProps { }
export interface TaglistState { tags: PopTag[]; }

export class Taglist extends React.Component<TaglistProps, TaglistState> {
    constructor(props : TaglistProps) {
        super(props);
        this.state = { tags: [] };
        this.random = Math.random();
    }
    random: number;
    componentDidMount() {
        Tags.update.addCallback({
            id: this.random + "_TaglistCallback",
            cb: () => this.setTags(Tags.tags)
        });
        Backend.Server.UpdateTags();
    }
    componentWillUnmount() {
        Tags.update.removeCallback(this.random + "_TaglistCallback");
    }
    render() {
        let list = this.state.tags.map(function(t) {
            return <Tag key={t.name} name={t.name} count={t.count} />;
        });
        return <div className="component-taglist"> { list } </div>
            ;
    }
    setTags(tags : PopTag[]) {
        if (tags.length === 0) {
            console.log("Got back zero tags");
        }
        console.log("Got " + tags.length + " tags");
        this.setState({ tags: tags });
    }
}

export default Taglist;



