import * as React from "react";
import Tags from '../state/Tags';
import * as Backend from '../Backend';
import '../styles/Tag';

export interface TagProps { name: string; count: number; }
export interface TagState { count: number; }

export class Tag extends React.Component<TagProps, TagState> {
    private random : number;
    constructor(props : TagProps) {
        super(props);
        this.random = Math.random();
        this.state = { count: this.props.count };
    }
    private getCallbackId() {
        return 'Tag_' + this.random;
    }
    componentDidMount() {
        Tags.update.addCallback
        (
            {
                id: this.getCallbackId(),
                cb: () => this.updateCount(Tags.getTag(this.props.name).count)
            }
        );
    }
    componentWillUnmount() {
        Tags.update.removeCallback(this.getCallbackId());
    }
    render() {
        let hc = (e : any) => this.handleClicked(e);
        return <div className="component-tag">
                <button onClick={hc}><div className="component-tag-name">{this.props.name}</div><div className="component-tag-count">{this.state.count}</div></button>
            </div>
            ;
    }
    updateCount(n : number) {
        this.setState({ count: n });
    }
    handleClicked(event : any) {
        if (this.state.count === 0) {
            console.log("Deleting empty tag " + this.props.name);
            Backend.Server.DeleteTag(Tags.getTag(this.props.name));
            return;
        }
        Backend.Server.SearchForTag(Tags.getTag(this.props.name));
    }
}

export default Tag;



