import * as React from "react";
import * as ReactDOM from 'react-dom';
import * as Backend from '../Backend';
import PopSong from '../pop/Song'
import '../styles/Song';
import * as Ui from '../state/Ui';
import Signals from '../state/Signals';
import Tags from '../state/Tags';
import Queue from '../state/Queue';
import Player from '../state/Player';
// I don't really know what this WithContext thing means
import { WithContext as ReactTags } from 'react-tag-input'

export interface SongProps { song : PopSong; playlistIndex : number; field: string; }
export interface SongState {
    song : PopSong;
    mode : Ui.QueueButtonMode;
    field: string;
    playing: boolean;
    current: boolean;
    selected: boolean;
}

interface ReactTagsTag { id: string; text: string; }

export class Song extends React.Component<SongProps, SongState> {
    constructor(props : SongProps) {
        super(props);
        this.callbackId = 'Song_' + props.playlistIndex + '_' + Math.random();
        this.state = {
            song: props.song,
            mode: Ui.Ui.getQueueButtonMode(),
            field: props.field,
            playing: false,
            current: false,
            selected: false
        };
        this._mouseWithin = false;
    }
    render() {
        let click = (e : any) => this.doerClicked(e);
        let cmenu = (e : any) => this.doerContextMenu(e);
        let enter = (e : any) => this.onMouseEnter(e);
        let leave = (e : any) => this.onMouseLeave(e);

        let tags : ReactTagsTag[] = this.state.song.tags.map(t => { return { id: t.name, text: t.name } });
        let suggestions : string[] = Tags.tags.map(t => t.name);
        let tdel = (i : number) => this.deleteTagAtIndex(i);
        let tadd = (t : string) => this.addTag(t);
        let noop = () => {};
        let tagsField = () =>
            <ReactTags
                tags={tags}
                suggestions={suggestions}
                handleDelete={tdel}
                handleAddition={tadd}
                handleDrag={noop}
            />;

        let field = (() => {
            switch (this.state.field) {
                case 'album':
                    return <div>{this.state.song.album}</div>
                case 'artist':
                    return <div>{this.state.song.artist}</div>
                case 'title':
                    return <div>{this.state.song.title}</div>
                case 'album':
                    return <div>{this.state.song.album}</div>
                case 'year':
                    return <div>{this.state.song.year}</div>
                case 'length':
                    return <div>{this.state.song.length}</div>
                case 'track':
                    return <div>{this.state.song.track}</div>
                case 'id':
                    return <div>{this.state.song.id}</div>
                case 'format':
                    return <div>{this.state.song.format}</div>
                case 'bitrate':
                    return <div>{this.state.song.bitrate}</div>
                case 'tags':
                default:
                    return tagsField();
            }
        })();

        let classes = ["component-song"];

        if (this.state.selected === true)
            classes.push("component-song-selected");
        if (this.state.playing === true)
            classes.push("component-song-playing");
        if (this.state.current === true)
            classes.push("component-song-current");

        let textClasses = ["component-song-text"];
        if (this.state.selected === true)
            textClasses.push("component-song-selected");
        if (this.state.playing === true)
            textClasses.push("component-song-playing");
        if (this.state.current === true)
            textClasses.push("component-song-current");

        let clickText = () => {
            Backend.Server.SetCmusPlaylistPos(this.props.playlistIndex);
        }

        // Might just get rid of the doer button
        // <button onContextMenu={cmenu} onClick={click}>{Ui.Ui.getQueueButtonModeString()}</button>
        return <div onMouseEnter={enter} onMouseLeave={leave} className={classes.join(' ')}>
            <button onClick={clickText} className={textClasses.join(' ')}>{ this.state.song.artist + " - " + this.state.song.title }</button>
            <div className="component-song-field">{field}</div>
            </div>
            ;
    }

    private callbackId : string;
    componentDidMount() {
        Ui.Ui.queueButtonModeChanged.addCallback
        (
            {
                id: this.callbackId,
                cb: () => this.setMode(Ui.Ui.getQueueButtonMode())
            }
        );
        Signals.trackShowEvent.addCallback
        (
            {
                id: this.callbackId,
                cb: () => this.trackShow()
            }
        );
        Signals.fieldSelectedEvent.addCallback
        (
            {
                id: this.callbackId,
                cb: () => this.setState(
                    {
                        field: Signals.fieldSelectedField
                    })
            }
        );
        Player.trackChange.addCallback
        (
            {
                id: this.callbackId,
                cb: () => this.setPlaying(this.state.song.path === Player.getPath())
            }
        );
        this.setPlaying(this.state.song.path === Player.getPath());

        Signals.selectCurrentSongEvent.addCallback
        (
            {
                id: this.callbackId,
                cb: () =>
                {
                    if (this._mouseWithin === true)
                    {
                        this.setSelected(!this.state.selected);
                    }
                }
            }
        );

        Signals.selectAllSongsEvent.addCallback
        (
            {
                id: this.callbackId,
                cb: () =>
                {
                    this.setSelected(true);
                }
            }
        );

        Signals.deselectAllSongsEvent.addCallback
        (
            {
                id: this.callbackId,
                cb: () =>
                {
                    this.setSelected(false);
                }
            }
        );

        Signals.stashSelectedSongsEvent.addCallback
        (
            {
                id: this.callbackId,
                cb: () =>
                {
                    if (this.state.selected)
                    {
                        Backend.Server.AddSongToStash(this.state.song);
                        this.setSelected(false);
                    }
                }
            }
        );

        Signals.unstashSelectedSongsEvent.addCallback
        (
            {
                id: this.callbackId,
                cb: () =>
                {
                    if (this.state.selected)
                    {
                        Backend.Server.RemoveSongFromStash(this.state.song);
                        this.setSelected(false);
                    }
                }
            }
        );
    }
    componentWillUnmount() {
        Ui.Ui.queueButtonModeChanged.removeCallback(this.callbackId);
        Signals.trackShowEvent.removeCallback(this.callbackId);
        Signals.fieldSelectedEvent.removeCallback(this.callbackId);
        Player.trackChange.removeCallback(this.callbackId);
        Signals.selectCurrentSongEvent.removeCallback(this.callbackId);
        Signals.selectAllSongsEvent.removeCallback(this.callbackId);
        Signals.deselectAllSongsEvent.removeCallback(this.callbackId);
        Signals.stashSelectedSongsEvent.removeCallback(this.callbackId);
        Signals.unstashSelectedSongsEvent.removeCallback(this.callbackId);
    }
    _newState() : SongState {
        return {
            song: this.state.song,
            mode: this.state.mode,
            field: this.state.field,
            playing: this.state.playing,
            current: this.state.current,
            selected: this.state.selected
        };
    }
    setSong(song : PopSong) {
        this.setState({ song: song });
    }
    setMode(mode : Ui.QueueButtonMode) {
        this.setState({ mode: mode });
    }
    setPlaying(b : boolean)
    {
        if (b !== this.state.playing)
        {
            this.setState({ playing: b });
        }
    }
    setCurrent(b : boolean)
    {
        if (b !== this.state.current)
        {
            this.setState({ current: b });
        }
    }
    setSelected(b : boolean)
    {
        if (b !== this.state.selected)
        {
            this.setState({ selected: b });
        }
    }
    doerContextMenu(event : any) {
        Ui.Ui.nextQueueButtonMode();
        event.preventDefault();
    }
    doerClicked(event : any) {
        switch(this.state.mode) {
            case Ui.QueueButtonMode.play:
                Backend.Server.SetCmusPlaylistPos(this.props.playlistIndex);
                break;
            case Ui.QueueButtonMode.stash:
                if (Queue.isStash)
                    Backend.Server.RemoveSongFromStash(this.state.song);
                else
                    Backend.Server.AddSongToStash(this.state.song);
                break;
        }
    }
    _mouseWithin : boolean;
    onMouseEnter(event : any) {
        Signals.trackInfo(this.state.song);
        this._mouseWithin = true;
    }
    onMouseLeave(event : any) {
        this._mouseWithin = false;
    }
    deleteTagAtIndex(i : number) {
        if (i >= 0 && i < this.state.song.tags.length) {
            Backend.Server.UntagSong(this.state.song.tags[i], this.state.song);
            this.setState(this._newState());
        }
    }
    addTag(name : string) {
        let tag = Tags.getTag(name);
        if (tag) {
            console.log("Tagging " + tag.name);
            Backend.Server.TagSong(tag, this.state.song);
            this.setState(this._newState());
        } else {
            console.log(name + " is not a tag");
        }
    }
    private topNextUpdate : boolean = false;
    componentDidUpdate() {
        if (this.topNextUpdate === true) {
            this.topNextUpdate = false;
            document.body.scrollTop = 0; // For Chrome, Safari and Opera 
            document.documentElement.scrollTop = 0; // For IE and Firefox
        }
    }
    setField(field : string) {
        this.topNextUpdate = true;
        this.setState({ field: field });
    }
    trackShow() {
        if (this.state.song.path === Signals.trackShowPath) {
            const element = ReactDOM.findDOMNode(this);
            const elementRect = element.getBoundingClientRect();
            const absoluteElementTop = elementRect.top + window.pageYOffset;
            const middle = absoluteElementTop - (window.innerHeight / 2);
            window.scrollTo(0, middle);
        }
    }
}

export default Song;




