import Song from '../pop/Song'
import * as Events from './Event'

class _Queue {
    update : Events.Event;
    isStash : boolean;
    songs : Song[];
    constructor() {
        this.songs = [];
        this.isStash = false;
        this.update = new Events.Event("QueueUpdated");
    }
    setStashSongs(songs: Song[]) {
        this.songs = songs;
        this.isStash = true;
        this.update.trigger();
    }
    setSongs(songs: Song[]) {
        this.songs = songs;
        this.isStash = false;
        this.update.trigger();
    }
}
const Queue = new _Queue();
export default Queue;



