// This is the only module that doesn't export a state object. Instead, it's a supporting
// class used by all the state objects. Might be worth creating a 'util' directory for
// stuff like this.
export interface EventCallback { id: string; cb: () => void; }
export class Event {
    private name: string;
    callbacks: EventCallback[];
    count: number;
    constructor(name : string) {
        this.name = name;
        this.callbacks = [];
        this.count = 0;
    }
    getName() {
        return this.name;
    }
    addCallback(cb : EventCallback) {
        this.callbacks.push(cb);
    }
    removeCallback(id : string) {
        let i = 0;
        for (i = 0; i < this.callbacks.length; ++i) {
            if (this.callbacks[i].id == id) {
                break;
            }
        }
        if (i >= 0 && i < this.callbacks.length) {
            this.callbacks.splice(i, 1);
        }
    }
    trigger() {
        ++this.count;
        this.callbacks.forEach(cb => cb.cb());
    }
}



