import Song from '../pop/Song'
import * as Events from './Event'

// holds the result of the last query
class _Query {
    update: Events.Event;
    songs : Song[];
    constructor() {
        this.songs = [];
        this.update = new Events.Event("QueryUpdated");
    }
    setSongs(songs: Song[]) {
        this.songs = songs;
        this.update.trigger();
    }
}
const Query = new _Query();
export default Query;



