import PopPlayer from '../pop/Player'
import * as Events from './Event'

class _Player {
    state : PopPlayer;
    update : Events.Event;
    trackChange : Events.Event;
    startPlaying : Events.Event;
    pause : Events.Event;
    constructor() {
        this.state = new PopPlayer();
        this.update = new Events.Event("PlayerUpdated");
        this.trackChange = new Events.Event("TrackChanged");
        this.startPlaying = new Events.Event("StartedPlaying");
        this.pause = new Events.Event("Paused");

    }
    setState(p : PopPlayer) {
        // trigger events
        let prev = this.state;
        this.state = p;
        if (this.trackChanged(prev)) {
            this.trackChange.trigger();
        }
        if (this.nowPlaying(prev)) {
            this.startPlaying.trigger();
        }
        else if (this.nowPaused(prev)) {
            this.pause.trigger();
        }
        this.update.trigger();
    }

    private trackChanged(prev : PopPlayer) {
        if (prev === null && this.state !== null) {
            return true;
        }
        if (prev.artist !== this.state.artist) {
            return true;
        } else if (prev["title"] !== this.state["title"]) {
            return true;
        }
        return false;
    }

    // true if we weren't playing before, but are now
    private nowPlaying(prev : PopPlayer) {
        var playing = (this.state !== null && this.state.status === "playing");
        var was = (prev !== null && prev.status === "playing");
        return playing && !was;
    }

    // true if we WERE playing beofre, but are NOT now
    private nowPaused(prev : PopPlayer) {
        var paused = (this.state !== null && this.state.status !== "playing");
        var was = (prev !== null && prev.status !== "playing");
        return paused && !was;
    }

    getStatus() : string { return this.state.status; }
    getTitle() : string { return this.state.title; }
    getArtist() : string { return this.state.artist; }
    getAlbum() : string { return this.state.album; }
    getPath() : string { return this.state.path; }
    getDuration() : number { return this.state.duration; }
    getPosition() : number { return this.state.position; }

    // true if we are playing any song
    isPlaying() {
        if (this.state.status === "playing") {
            return true;
        } else {
            return false;
        }
    }
}
const Player = new _Player();
export default Player;



